a-zA-Z-, ])*$/\n                ),\n            },\n            age: {\n                maxLength: maxLength(3),\n                numeric,\n            },\n            gender: {},\n            relation: {\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            email: {\n                required,\n                email,\n            },\n            phone: {\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\n                numeric,\n            },\n            mobile: {\n                required,\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\n                numeric,\n            },\n            mobile1: {\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\n                numeric,\n            },\n            whatsup: {\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\n                numeric,\n            },\n        },\n        guestHome: {\n            complexName: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            floor: {\n                required,\n                minLength: minLength(1),\n            },\n            doorNo: {\n                required,\n                minLength: minLength(1),\n                numeric,\n            },\n            streetNo: {\n                required,\n                minLength: minLength(3),\n            },\n            area: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            district: {\n                required,\n                minLength: minLength(1),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            state: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            pin: {\n                required,\n                maxLength: maxLength(6),\n                numeric,\n            },\n            zone: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            country: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            reachus: { required, url },\n        },\n        guestOffice: {\n            officecompanyName: {\n                required,\n                minLength: minLength(3),\n                alpha_num: helpers.regex(\"alpha_num\", /^[\\w\\-\\s]+$/),\n            },\n            officecomplexName: {\n                required,\n                minLength: minLength(3),\n                alpha_num: helpers.regex(\"alpha_num\", /^[\\w\\-\\s]+$/),\n            },\n            officefloor: {\n                required,\n                minLength: minLength(3),\n            },\n            officedoorNo: {\n                required,\n                minLength: minLength(1),\n                numeric,\n            },\n            officestreetNo: {\n                required,\n                minLength: minLength(3),\n            },\n            officearea: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            officedistrict: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            officestate: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            officepin: {\n                required,\n                maxLength: maxLength(6),\n                numeric,\n            },\n            officezone: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            officecountry: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            officereachus: { required, url },\n        },\n        guestFamily: {\n            familyname: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            familyage: {\n                required,\n                maxLength: maxLength(3),\n                numeric,\n            },\n            familygender: {\n                required,\n            },\n            familyrelation: {\n                required,\n                minLength: minLength(3),\n                alpha: helpers.regex(\n                    \"alpha\",\n                    /^[a-zA-Z-,]+(\\s{0,1}[a-zA-Z-, ])*$/\n                ),\n            },\n            familymobile: {\n                required,\n                digits_validate: helpers.regex(\"digits_validate\", /^\\d{10}$/),\n                numeric,\n            },\n        },\n    },\n    methods: {\n        checkImage(url) {\n            var request = new XMLHttpRequest();\n            request.open(\"GET\", url, true);\n            request.send();\n            request.onload = function () {\n                let status = request.status;\n                if (request.status == 200) {\n                    //if(statusText == OK)\n                    return true;\n                } else {\n                    return false;\n                }\n            };\n        },\n        deleteFamily(e, id) {\n            let _this = this;\n            this.loader = true;\n            axios\n                .delete(`/api/guest/profile/${id}`)\n                .then(function (response) {\n                    _this.loader = false;\n                    _this.message = \"Deleted\";\n                    console.log(response.data);\n                    _this.familyList = response.data;\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.loader = false;\n                    console.log(error.response.data.errors);\n                });\n        },\n        editFamilyForm(e, id) {\n            let _this = this;\n            _this.$v.guestFamily.$reset();\n            if (id == \"new\") {\n                for (const key in this.guestFamily) {\n                    _this.guestFamily[key] = null;\n                }\n\n                _this.familyimageOne = null;\n                _this.familyimageTwo = null;\n                _this.familyimageOnePreview = null;\n                _this.familyimageTwoPreview = null;\n                $(\"#guestfamilyimagetwo\").val(\"\");\n                $(\"#guestfamilyimageone\").val(\"\");\n                _this.guestFamily.guest_id = _this.guestId;\n                let txt = $(\"#addClose\").text().trim();\n                if (txt == \"Add\") {\n                    $(\"#addClose\").text(\"Close\");\n                }\n                if (txt == \"Close\") {\n                    $(\"#addClose\").text(\"Add\");\n                }\n                $(\".guest-details-family-form-cont\").slideToggle(\"linear\");\n                _this.fedit = false;\n            } else {\n                let txt = $(\"#addClose\").text().trim();\n                if (txt == \"Add\") {\n                    $(\"#addClose\").text(\"Close\");\n                }\n                if (txt == \"Close\") {\n                    $(\"#addClose\").text(\"Add\");\n                }\n                $(\".guest-details-family-form-cont\").slideDown(\"linear\");\n\n                _this.guestFamily = { ..._this.familyList[id] };\n                _this.fedit = true;\n                //console.log(_this.guestFamily);\n            }\n        },\n        uploadImg(e, type) {\n            if (type == \"profile\") {\n                this.sendProfile_imageOne = e.target.files[0];\n                this.profilePicPreview = URL.createObjectURL(\n                    this.sendProfile_imageOne\n                );\n                if (this.sendProfile.imageOne) {\n                    delete this.sendProfile.imageOne;\n                }\n            }\n\n            if (type == \"company\") {\n                this.guestProfile_logo = e.target.files[0];\n                this.companyPicPreview = URL.createObjectURL(\n                    this.guestProfile_logo\n                );\n                if (this.guestProfile.imageOne) {\n                    delete this.guestProfile.imageOne;\n                }\n            }\n\n            if (type == \"familyOne\") {\n                this.familyimageOne = e.target.files[0];\n                this.familyimageOnePreview = URL.createObjectURL(\n                    this.familyimageOne\n                );\n            }\n            if (type == \"familyTwo\") {\n                this.familyimageTwo = e.target.files[0];\n                this.familyimageTwoPreview = URL.createObjectURL(\n                    this.familyimageTwo\n                );\n            }\n        },\n        // editForm(e) {\n        //     let id = e.currentTarget.parentElement.parentElement.id;\n        //     $(`#${id} .ven-profile-form-submit button[type=submit]`).css(\n        //         \"display\",\n        //         \"block\"\n        //     );\n        //     $(`#${id} input`).removeAttr(\"disabled\");\n        //     $(`#guest-logo`).removeAttr(\"required\");\n        //     $(\"#company-logo\").removeAttr(\"required\");\n        //     $(`#${id} input`).eq(1).focus();\n        //     if (id == \"guest-details\") {\n        //         this.pedit = true;\n        //         this.pprofileDisabled = false;\n        //     }\n        //     if (id == \"company-details\") {\n        //         this.cedit = true;\n        //         this.cprofileDisabled = false;\n        //     }\n        //     if (id == \"office-details\") {\n        //         this.oedit = true;\n        //         this.oprofileDisabled = false;\n        //     }\n        // },\n        toggleForm(e, bool) {\n            //console.log(bool);\n            let id = null;\n            if (bool == true) {\n                id = e.currentTarget.dataset.id;\n            }\n            if (bool == false) {\n                id = e;\n            }\n            this.showForm = true;\n            this.$nextTick(function () {\n                const btns = document.querySelectorAll(\".tab-btn\");\n      